<template>
    <div id="manageResource">
        <el-input
            placeholder=""
            v-model="searchKeyword"
            clearable
            width="300"
            @input="searchKeywordChange">
        </el-input>
        <el-select v-model="searchType" placeholder="" @change="searchTypeChange">
            <el-option
                v-for="item in searchOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-button type="primary" class="uploadButton" @click="showUploadDialog()"></el-button>
        <el-table
            :data="listData"
            style="width: 100%">
            <el-table-column
                prop="name"
                label="">
            </el-table-column>
            <el-table-column
                prop="teacher"
                label=""
                width="180">
            </el-table-column>
            <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                    {{ scope.row.create_time | getTime }}
                </template>
            </el-table-column>
            <el-table-column
                width="180"
                label="">
                <template slot-scope="scope">
                    <a :href="scope.row.address" class="download" download></a>
                    <el-button type="danger" size="small" :disabled="username !='' && scope.row.teacher != username" @click="deleteResource(scope)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="totalResource">
            </el-pagination>
        </div>
        <el-dialog title="" :visible.sync="dialogUploadVisible" label-width="100px" label-position="left">
            <el-upload
                class="upload-demo"
                ref="upload"
                multiple
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-exceed="handelExceed"
                :file-list="uploadFileList"
                :with-credentials="true"
                :limit="1"
                :before-upload="beforeUploadFileHandler"
                :on-success="onSuccessFileHandler"
                :auto-upload="false">
                <el-button style="margin-left: 10px;" slot="trigger" size="small" type="primary"></el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
            </el-upload>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'ManageResource',
    data: function () {
        return {
            listData: [],
            httpAddress: '/api',
            currentPage: 0,
            page: 0,
            totalPage: 0,
            totalResource: 0,
            searchKeyword: '',
            searchType: 'name',
            searchOptions: [
                {
                    value: 'name',
                    label: '',
                    key: 'name'
                },
                {
                    value: 'teacher',
                    label: '',
                    key: 'teacher'
                },
            ],
            dialogUploadVisible: false,
            uploadFileList: [],
            uploadUrl: '/api/research/uploadResource',
            username: '',
        }
    },
    filters: {
        getTime: function(val) {
            let date = new Date(parseInt(val));
            return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
        }
    },
    mounted: function() {
        this.getResource();
        this.username = sessionStorage.getItem('username');
    },
    methods: {
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            // console.log(`: ${val}`);
            this.page = val - 1;
            this.getResource();
        },
        searchKeywordChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getResource();
        },
        searchTypeChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getResource();
        },
        showUploadDialog() { // 
            this.dialogUploadVisible = true;
        },
        beforeUploadFileHandler() {
            // console.log(file);
        },
        handelExceed() {
            this.$message({
                type: 'warning',
                message: ''
            })
        },
        onSuccessFileHandler() { // 
            this.$message({
                type: 'success',
                message: ''
            })
            this.$refs.upload.clearFiles();
            this.getResource();
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteResource(scope) {
            this.$http.post(`${this.httpAddress}/research/deleteResource`, 
                {
                    filename: scope.row.name,
                    teacher: scope.row.username
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.getResource();
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message({
                            type: 'warning',
                            message: ''
                        })
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
        getResource() {
            this.$http.post(`${this.httpAddress}/research/getResource`, 
                {
                    page: this.page,
                    type: this.searchType,
                    keyword: this.searchKeyword
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.listData = data.data.data;
                        this.totalResource = data.data.total;
                        this.totalPage = Math.ceil(data.data.total / 10);
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
    }
}
</script>

<style scoped>
.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.el-input {
    width: 300px;
    margin-right: 20px;
}
.el-select {
    margin-right: 20px;
}
.download {
    display: inline-block;
    width: 56px;
    text-align: center;
    height: 32px;
    line-height: 32px;
    font-size: 12px;
    background: #409EFF;
    border-radius: 4px;
    color: #ffffff;
    margin-right: 10px;
}
</style>