<template>
    <div id="manageCourse">
        <div class="searchClass">
            <el-input
                placeholder=""
                v-model="searchKeyword"
                clearable
                width="300"
                @input="searchKeywordChange">
            </el-input>
            <el-select v-model="searchType" placeholder="" @change="searchTypeChange">
                <el-option
                    v-for="item in searchOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="searchPeriod" clearable placeholder="" @change="searchTypeChange">
                <el-option
                    v-for="item in periodOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <el-button type="primary" @click="addNewCourse"></el-button>
        </div>
        <el-table
            :data="listData"
            stripe
            style="width: 100%">
            <el-table-column
                prop="course_id"
                label=""
                width="110">
            </el-table-column>
            <el-table-column
                prop="course_name"
                label="">
            </el-table-column>
            <el-table-column
                prop="course_teacher"
                label=""
                width="110">
            </el-table-column>
            <el-table-column
                prop="course_teacher_id"
                label="ID">
            </el-table-column>
            <el-table-column
                prop="study_class"
                width="150"
                label="">
            </el-table-column>
            <el-table-column
                prop="note"
                width="100"
                label="">
            </el-table-column>
            <el-table-column
                width="180"
                label="">
                <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-document" @click="viewStudentList(scope)" size="mini"></el-button>
                    <el-tooltip class="item" effect="dark" content="" placement="top-start">
                        <el-button type="danger" icon="el-icon-document-add" class="buttonImportStudentList" @click="importStudentList(scope)" size="mini"></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
            <el-table-column
                width="180"
                label="">
                <template slot-scope="scope">
                    <el-button icon="el-icon-edit" @click="editCourse(scope)" size="mini"></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="deleteCourse(scope)" size="mini"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="totalCourse">
            </el-pagination>
        </div>
        <el-dialog title="" :visible.sync="dialogViewStudentListVisible" width="800px">
            <el-button type="primary" @click="addStudent"></el-button>
            <el-table
                :data="nowStudentList"
                style="width: 100%">
                <el-table-column
                    prop="username"
                    label=""
                    width="120">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label=""
                    width="100">
                </el-table-column>
                <el-table-column
                    label=""
                    width="50">
                    <template slot-scope="scope">
                        {{ scope.row.sex == 1 ? '' : '' }}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="class"
                    label=""
                    width="100">
                </el-table-column>
                <el-table-column
                    prop="major"
                    label="">
                </el-table-column>
                <el-table-column
                    prop="academy"
                    label="">
                </el-table-column>
                <el-table-column
                    prop="grade"
                    label=""
                    width="70">
                </el-table-column>
                <el-table-column
                    label=""
                    width="100">
                    <template slot-scope="scope">
                        <el-button class="buttonDeleteStudent" type="danger" @click="deleteStudent(scope)" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogEditCourseVisible" width="530px">
            <el-form class="nowEditData" :model="nowEditData">
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowEditData.course_name" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowEditData.course_teacher" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowEditData.course_teacher_id" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowEditData.note" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitEditCourse"></el-button>
                <el-button @click="cancelDialog('dialogEditCourseVisible')"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogAddCourseVisible" width="530px">
            <el-form class="nowAddData" :model="nowAddData">
                <el-form-item>
                   <span class="loginLabel"></span><el-input v-model="nowAddData.course_id" autocomplete="off" class="input"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowAddData.course_name" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowAddData.study_class" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowAddData.course_teacher" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowAddData.course_teacher_id" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
                <el-form-item>
                    <span class="loginLabel"></span><el-input v-model="nowAddData.note" autocomplete="off" class="inputCourse"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitAddCourse"></el-button>
                <el-button @click="cancelDialog('dialogAddCourseVisible')"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'ManageCourse',
    data: function () {
        return {
            httpAddress: '/api',
            listData: [],
            currentPage: 0,
            page: 0,
            totalPage: 0,
            totalCourse: 0,
            searchKeyword: '',
            searchType: 'name',
            searchPeriod: '',
            searchOptions: [
                {
                    value: 'name',
                    label: '',
                    key: 'name'
                },
                {
                    value: 'teacher',
                    label: '',
                    key: 'teacher'
                },
            ],
            periodOption: [],
            dialogViewStudentListVisible: false,
            dialogAddCourseVisible: false,
            dialogEditCourseVisible: false,
            nowStudentList: [], // 
            nowViewData: {
                course_id: '',
                study_class: '',
            },
            nowEditData: {
                course_id: '',
                course_name: '',
                study_class: '',
                course_teacher: '',
                course_teacher_id: '',
                note: ''
            },
            nowAddData: {
                course_id: '',
                course_name: '',
                study_class: '',
                course_teacher: '',
                course_teacher_id: '',
                note: ''
            },
        }
    },
    mounted: function () {
        this.$http.get(`${this.httpAddress}/research/getPeriod`)
            .then(res => {
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    this.periodOption = data.data.map(item => {
                        return {
                            value: item.period,
                            label: item.period,
                            key: item.period,
                        }
                    })
                } else {
                    this.$message({
                        message: data.msg,
                        type: 'warning'
                    });
                }
            })
            .catch((err) => {
                console.log('err', err);
            })
        this.$http.post(`${this.httpAddress}/research/getCourseRoot`, 
            {
                keyword: this.searchKeyword,
                type: this.searchType,
                page: this.page,
                period: this.searchPeriod,
            })
            .then((res) => {
                // console.log('res', res);
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    this.listData = data.data.data;
                    this.totalCourse = data.data.total;
                    this.totalPage = Math.ceil(data.data.total / 10);
                }
            })
            .catch((err) => {
                console.log('err', err);
            })
    },
    methods: {
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            this.page = val - 1;
            this.getCourse();
        },
        searchKeywordChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getCourse();
        },
        searchTypeChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getCourse();
        },
        addNewCourse() {
            this.dialogAddCourseVisible = true;
        },
        submitAddCourse() {
            this.$http.post(`${this.httpAddress}/research/operateCourse`, {
                    action: 'add',
                    course_id: this.nowAddData.course_id,
                    study_class: this.nowAddData.study_class,
                    course_name: this.nowAddData.course_name,
                    course_teacher: this.nowAddData.course_teacher,
                    course_teacher_id: this.nowAddData.course_teacher_id,
                    note: this.nowAddData.note
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.getCourse();
                        this.dialogAddCourseVisible = false;
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message({
                            type: 'warning',
                            message: ''
                        })
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                    this.dialogAddCourseVisible = false;
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                })
        },
        deleteCourse(scope) {
            console.log(scope);
            this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$http.post(`${this.httpAddress}/research/operateCourse`, {
                        action: 'delete',
                        course_id: scope.row.course_id,
                        study_class: scope.row.study_class,
                    })
                    .then((res) => {
                        let data = res.data;
                        if (data.status == 0 && data.msg == 'ok') {
                            this.getCourse();
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                        } else {
                            this.$message({
                                type: 'warning',
                                message: ''
                            })
                        }
                    })
                    .catch((err) => {
                        console.log('err', err);
                        this.$message({
                            type: 'warning',
                            message: ''
                        })
                    })
                }).catch(() => {
                    // this.$message({
                    //     type: 'info',
                    //     message: ''
                    // });          
                });
            
        },
        editCourse(scope) {
            this.nowEditData = {
                course_id: scope.row.course_id,
                study_class: scope.row.study_class,
                course_name: scope.row.course_name,
                course_teacher: scope.row.course_teacher,
                course_teacher_id: scope.row.course_teacher_id,
                note: scope.row.note
            }
            this.dialogEditCourseVisible = true;
        },
        submitEditCourse() {
            this.$http.post(`${this.httpAddress}/research/operateCourse`, {
                    action: 'edit',
                    course_id: this.nowEditData.course_id,
                    study_class: this.nowEditData.study_class,
                    course_name: this.nowEditData.course_name,
                    course_teacher: this.nowEditData.course_teacher,
                    course_teacher_id: this.nowEditData.course_teacher_id,
                    note: this.nowEditData.note
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.getCourse();
                        this.dialogEditCourseVisible = false;
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message({
                            type: 'warning',
                            message: ''
                        })
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                    this.dialogEditCourseVisible = false;
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                })
        },
        getCourse() {
            this.$http.post(`${this.httpAddress}/research/getCourseRoot`, {
                    keyword: this.searchKeyword,
                    type: this.searchType,
                    page: this.page,
                    period: this.searchPeriod,
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.listData = data.data.data;
                        this.totalCourse = data.data.total;
                        this.totalPage = Math.ceil(data.data.total / 10);
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
        viewStudentList(scope) {
            this.$http.post(`${this.httpAddress}/research/getStudentList`, {
                    study_class: scope.row.study_class,
                    course_id: scope.row.course_id,
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.nowStudentList = data.data;
                        this.nowViewData.course_id = scope.row.course_id;
                        this.nowViewData.study_class = scope.row.study_class;
                        this.dialogViewStudentListVisible = true;
                    } else {
                        this.$message({
                            message: `${data.msg}`,
                            type: 'warning'
                        });
                    }   
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
        getStudentList() {
            this.$http.post(`${this.httpAddress}/research/getStudentList`, {
                    study_class: this.nowViewData.study_class,
                    course_id: this.nowViewData.course_id,
                })
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.nowStudentList = data.data;
                    } else {
                        this.$message({
                            message: `${data.msg}`,
                            type: 'warning'
                        });
                    }   
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
        importStudentList(scope) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let loading = this.$loading({
                    lock: true,
                    text: '',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });
                this.$http.post(`${this.httpAddress}/research/importStudentList`, 
                    {
                        teacher_id: scope.row.teacher_id,
                        study_class: scope.row.study_class,
                        course_id: scope.row.course_id
                    })
                    .then(res => {
                        let data = res.data;
                        if (data.status == 0 && data.msg == 'ok') {
                            this.listData[scope.$index + (this.currentPage - 1) * 10].course_student = data.daa;
                            this.$message({
                                message: `${scope.row.course_name}`,
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: `${scope.row.course_name}${data.msg}`,
                                type: 'warning'
                            });
                        }
                        loading.close();
                    })
                    .catch(err => {
                        console.log(err);
                        loading.close();
                    })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
            
        },
        deleteStudent(scope) {
            this.$http.post(`${this.httpAddress}/research/operateStudentFromCourse`, 
                {
                    action: 'delete',
                    course_id: this.nowViewData.course_id,
                    study_class: this.nowViewData.study_class,
                    username: scope.row.username
                })
                .then(res => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.$message({
                            message: ``,
                            type: 'success'
                        });
                        this.nowStudentList.splice(scope.$index, 1);
                    } else {
                        this.$message({
                            message: `${data.msg}`,
                            type: 'warning'
                        });
                    }
                })
                .catch(err => {
                    console.log(err);
                })
        },
        addStudent() {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
            }).then(({ value }) => {
                this.$http.post(`${this.httpAddress}/research/operateStudentFromCourse`, 
                    {
                        action: 'add',
                        course_id: this.nowViewData.course_id,
                        study_class: this.nowViewData.study_class,
                        username: value
                    })
                    .then(res => {
                        let data = res.data;
                        if (data.status == 0 && data.msg == 'ok') {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            // this.nowStudentList.push(data.data);
                            this.getStudentList();
                        } else {
                            this.$message({
                                message: `${data.msg}`,
                                type: 'warning'
                            });
                        }
                    })
                    .catch(err => {
                        console.log(err);
                        this.$message({
                            message: '',
                            type: 'warning'
                        });
                    })
            }).catch(() => {
                     
            });
        }
    }
}
</script>

<style scoped>
.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.el-input {
    width: 300px;
    margin-right: 20px;
}
.el-select {
    width: 150px;
    margin-right: 20px;
}
.nowEditData, .nowAddData {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.loginLabel {
    display: inline-block;
    margin-right: 20px;
}
.inputCourse {
    width: 300px;
}
.dialog-footer {
    display: flex;
    justify-content: center;
}
</style>
